@font-face {
  font-family: "Avenir Light";
  src: url(/static/media/Avenir-Light.33f6570a.ttf) format("truetype");
}

@font-face {
  font-family: "Avenir Heavy";
  src: url(/static/media/Avenir-Heavy.f080b689.ttf) format("truetype");
}

@font-face {
  font-family: "Anonymous Pro Bold";
  src: url(/static/media/Anonymous-Pro-Bold.1d171b53.ttf) format("truetype");
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.heading {
  font-family: "Avenir Heavy", sans-serif;
  font-size: 24px;
  margin: 0px 0px;
}

.subheading {
  font-family: "Avenir Light", sans-serif;
  font-size: 24px;
  margin: 0px 0px;
}

.no-select {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version supported by Chrome and Opera */
}

a.light-link:link {
  color: #0047c6;
  text-decoration: none;
}
a.light-link:visited {
  color: #0047c6;
  text-decoration: none;
}
a.light-link:hover {
  color: #65baee;
}

