.input-bracket {
  font-size: 82px;
}

#top-panel {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 20px;
}

#credits {
  position: fixed;
  bottom: 10px;
  left: 15px;
  font-family: "Avenir Light", sans-serif;
  font-size: 14px;
}

.pulse {
  background: -webkit-linear-gradient(225deg, #65baee, #002da4);
  background: linear-gradient(225deg, #65baee, #002da4);
  background-size: 400% 400%;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  animation: CoolGradient 2s ease infinite;
  -webkit-animation: CoolGradient 2s ease infinite;
  -moz-animation: CoolGradient 2s ease infinite;
}

@keyframes CoolGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@-webkit-keyframes CoolGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

